<template>
  <div class="my-tab-bar">
    <div
      class="tab-item"
      :class="{ current: active == index }"
      v-for="(item, index) in tabList"
      :key="item.id"
      @click="$emit('change', index)"
    >
      <span class="iconfont" :class="item.icon"></span>
      <span>{{ item.text }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    tabList: {
      type: Array,
      required: true,
      validator(value) {
        if (value.length >= 2 && value.length <= 5) {
          return true
        } else {
          console.error('数量必须在2-5个之间')
          return false
        }
      },
    },
    active: {
      type: Number,
      default: 0,
    },
  },
}
</script>

<style lang="less" scoped>
.my-tab-bar {
  position: fixed;
  background: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  border-top: 1px solid #ccc;
  display: flex;
  justify-content: space-around;
  align-items: center;
  .tab-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    .iconfont {
      font-size: 18px;
    }
  }
  .current {
    color: #1d7bff;
  }
}
</style>
