<template>
  <div class="my-goods-list">
    <div>商品列表</div>
    <my-table :data="goodsList">
      <template #header>
        <th>#</th>
        <th>商品名称</th>
        <th>价格</th>
        <th>标签</th>
        <th>操作</th>
      </template>
      <template #body="{row,index}">
        <td>{{ index + 1 }}</td>
        <td>{{ row.goods_name }}</td>
        <td>¥ {{ row.goods_price }}</td>
        <td>
          <input
            v-if="row.inputVisible"
            class="tag-input form-control"
            type="text"
            v-focus
            @blur="row.inputVisible = false"
            @keyup.enter="handleEnter(row, $event)"
            @keyup.esc="$event.target.value = ''"
          />
          <button
            @click="row.inputVisible = true"
            v-else
            class="btn btn-primary btn-sm add-tag"
          >
            +Tag
          </button>
          <span
            v-for="tag in row.tags"
            :key="tag"
            class="badge badge-pill badge-warning"
            >{{ tag }}</span
          >
        </td>
        <td>
          <button @click="del(row.id)" class="btn btn-danger btn-sm">
            删除
          </button>
        </td>
      </template>
    </my-table>
  </div>
</template>

<script>
import axios from 'axios'
import MyTable from './my-table'
export default {
  data() {
    return {
      // 商品列表数据
      goodsList: [],
    }
  },
  created() {
    this.getGoodsList()
  },
  components: {
    MyTable,
  },
  methods: {
    // 初始化商品的数据
    async getGoodsList() {
      // 发送ajax请求
      const res = await axios.get('https://www.escook.cn/api/goods')
      const { status, data } = res.data
      if (status !== 0) return console.log('获取商品列表失败')
      // 请求成功
      this.goodsList = data
      console.log(data)
    },
    // 删除
    del(id) {
      this.goodsList = this.goodsList.filter(v => v.id !== id)
    },
    handleBlur(row) {
      row.inputVisible = false
    },
    handleEnter(row, e) {
      if (e.target.value.trim() === '') return

      row.tags.push(e.target.value)
      e.target.value = ''
      row.inputVisible = false

      // if (e.target.value.trim() === '') return
      // row.tags.push(e.target.value)
      // e.target.value = ''
      // row.inputVisible = false
    },
  },
}
</script>

<style lang="less" scoped>
.my-goods-list {
  .badge {
    margin-right: 5px;
  }
}
</style>
